import {
  axiosGet,
  axiosPost
} from '@/libs/http'


import {
  appkey
} from '@/datas/key'

function getBanner() {
  return new Promise((resolve, reject) => {
    axiosGet({
      url: '/banner?',
      success(data) {
        resolve(data);
      },
      error(err) {
        reject(err);
      }
    })
  })
}

function getHotList() {
  return new Promise((resolve, reject) => {
    axiosGet({
      url: '/typeProducts?key=isHot&value=1',
      success(data) {
        resolve(data);
      },
      error(err) {
        reject(err);
      }
    })
  })
}

function getTypeList(t, value) {
  let type = new Promise((resolve, reject) => {
    axiosGet({
      url: `/typeProducts?key=${t}&value=${value}`,
      success(data) {
        resolve(data);
      },
      error(err) {
        reject(err)
      }
    })
  })
  return type;
}

