function LikeAndNotLike(type, list) {
  let temp = type ? 'like' : 'notlike';
  return new Promise((resolve, reject) => {
    axiosPost({
      url: `/${temp}`,
      data: list,
      success(data) {
        resolve(data);
      },
      error(err) {
        reject(err)
      }
    })
  })
}

function FindLike(token, pid) {
  let temp = pid ? 'findlike' : 'findAllLike';
  return new Promise((resolve, reject) => {
    axiosGet({
      url: `/${temp}?pid=${pid}&tokenString=${token}`,
      success(data) {
        resolve(data);
      },
      error(err) {
        reject(err)
      }
    })
  })
}

